<script>
import moment from 'moment'
import slider from '@/components/base/slider'
import baseProduct from '@/components/product/BaseProduct'
import marquee from '@/components/base/marquee'
import { getLocalStorage } from '@/assets/js/localStorage'
export default {

  name: 'home',
  data () {
    return {
      lists: [
        '',
      ],
      productInfo:{}
    }
  },
  mounted () {
    if (getLocalStorage('userInfo')) {
      this.memberId = getLocalStorage('userInfo').id
    }
    this.getDetail()
    let cc = this.diffDate('2018-04-09')
    let dd = this.judgeCondition('73')
  },
  methods: {
    getDetail () {
      let postData = {
        id: 1,
        memberId: this.memberId
      }
      this.$api.product.productDetail(postData).then(res => {
        console.log(321,res.data.product)
        this.productInfo = res.data.product
    this.parameters = res.data.product.parameter_values[0].entries
      })
    },
    diffDate (birthday) {
      let baseTime = '2019-04-04'
      let diffYears = moment(baseTime).diff(moment(birthday), 'year')
      let diffDays = moment(baseTime).diff(moment(birthday), 'days')
      if (diffYears < 0) return ''
      if (diffYears > 0) {
        return diffYears + ''
      }
      if (diffYears < 1 && diffYears >= 0) {
        return diffDays + ''
      }
    },
    judgeCondition (age) {
      if (age.includes('')) {
        if (parseInt(age) < 28) {
          console.log('nofuhe')
          return false
        } else {
          return true
        }
      } else {
        if (parseInt(age) > 75) {
          console.log('nofuhe')
          return false
        } else {
          return true
        }
      }
    }
  },
  components: { baseProduct,slider,marquee }
}
