<template>
  <div class="wrap" id="wrap">
    <div id="box">
      <div ref="marquee" class="marquee-text">{{text}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Marquee',
  props: ['lists'], //   [ """"]
  data () {
    return {
      text: '' // 
    }
  },
  methods: {
    move (textBar) {
      let pos = 0; let width = textBar.offsetWidth
      let dis = 15
      let timer = setInterval(() => {
        pos++
        textBar.style.transform = 'translateX(-' + pos + 'px)'
        if (pos - dis == width) {
          let clone = textBar.cloneNode(true)
          textBar.parentElement.appendChild(clone)
          this.move(clone)
        }
        if (pos > textBar.parentElement.offsetWidth + width) {
          textBar.remove()
          clearInterval(timer)
          textBar = timer = null
        }
      }, 30)
    }
  },
  // arr
  mounted: function () {
    for (let i = 0; i < this.lists.length; i++) {
      this.text += ' ' + this.lists[i]
    }
    this.$nextTick(() => {
      this.move(this.$refs.marquee)
    })
  }
}
</script>
<style scoped>
  .wrap {
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  #box {
    width: 100%;
    overflow: hidden;
    height: 38px;
  }
  .marquee-text{
    position: absolute;
    left: 100%;
    line-height: 38px;
    white-space: nowrap;
  }
  #box div {
    float: left;
  }

  #marquee {
    margin: 0 16px 0 0;
  }

  #node {
    position: absolute;
    z-index: -999;
    top: -999999px;
  }
</style>
